import React from 'react'
import {connect} from 'react-redux';


const Login= (props) => {
    return (
        <div>
           <input type="text"></input><br></br>
           <input type="password"></input><br></br>
           <button onClick={ () => {
                        return(props.dispatch({
                            type:'DL',
                            userinfo:'123'
                        }),
                        props.history.push('/')
                        )
                           
                        } }
                        >登陆</button>
        </div>)
}
                        
const aa = (state)=>{
    return {
        mydata:state.todo,
        change:state.change,
        userinfo:state.login
    }
}

export default connect(aa)(Login);



