import React from 'react';
import {connect} from 'react-redux';

class Sc extends React.Component {
    constructor(props) {
        super(props);
    }

    panduan(){
        if(this.props.sc.length<1){
            return (
                <div>
                    没有收藏的商品
                </div>
            )
        }else{
            console.log('sc not null:',this.props.sc)
            return(
                this.props.sc.map((item,index)=>{
                return(
                <div>
                   <img src={item.img} style={{width:300}}></img>
                   <button onClick={() => {
                       console.log('idx:',index);
                        this.props.dispatch({
                        type:'DEL',
                        idx:index
                        
                    })
                }
            }
            >删除</button>
                </div>
                )
            })
            )
           
           
        }
    }
    render(){

        return (
            
                this.panduan()
               
        )
    }
}
const scb = (state)=>{
    return {
        sc:state.sc,
        userinfo:state.login,
        index:state.change,
        datalist:state.todo
    }
}


export default connect(scb)(Sc);







