
    function gai(state=[],action){
        if(action.type=='SC'){
            return [...state,action.sc]
        }else if(action.type=='DEL'){
            //删除一个 dispatch 值，对应图片的下标 idx
            
            let arr=[...state];
            arr.splice(action.idx,1)
            return arr;
        }
        return state;
    }
    export default gai;


