import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import {connect} from 'react-redux';      

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }


    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>


                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang'></i>                           
                        }
                        title="收藏"
                        key="cart"
                        selected={ pathname === '/login' }
                        onPress={ () => {
                            return (this.props.userinfo
                                ? <div>
                                    {this.props.history.push('/sc')}
                                    </div>
                                :
                                        this.props.history.push('/login')
                                )
                                }}>
                        
                        
                    </TabBar.Item>


                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdanliebiao'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdanliebiao'></i>                           
                        }
                        title="订单列表"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                    >
                        
			        </TabBar.Item>


                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-wodetaobao'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-wodetaobao'></i>                           
                        }
                        title="我的淘宝"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                    >
                    </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}


const iu = (state)=>{
    return {
        sc:state.sc,
        userinfo:state.login,
        index:state.change,
        datalist:state.todo
    }
}

export default connect(iu)(withRouter(Mytab));