import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import {Fragments} from 'react';
import {connect} from 'react-redux';
class Buyit extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
            str:'iconfont icon-shoucang'
        };
    }
    getlist=()=>{
        const list=this.props.sc;
        const idx=this.props.index;
        const obj=this.props.datalist[idx];
        console.log('obj:',obj);
        console.log('scscscsc:',list);
        if(list.length<1){
            this.props.dispatch({
                type:'SC',
                sc:obj

            })
            this.setState({
                str:'iconfont icon-shoucang-copy'
            })
        }else{
            //遍历循环 sc  obj.id sc.id
            for(var i=0;i<list.length;i++){
                if(obj.id!==list.id){
                    this.props.dispatch({
                            type:'SC',
                            sc:obj
                    })
                    //
                    this.setState({
                        str:'iconfont icon-shoucang-copy'
                    })
                    break;
                }
            }
        }

   }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0
            }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="home"
                        icon={
                            <i className='iconfont  icon-dianpu'></i>
                        }
                        selectedIcon={
                            <i className='picpicpic icon-dianpu'></i>
                        }
                        selected={pathname === '/'}
                        onPress={() => {
                            //  this.props.history.push('/');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>


                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-kefu'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-kefu'></i>
                        }
                        title="客服"
                        key="cart"
                        selected={pathname === '/cart'}
                        onPress={() => {
                            this.props.history.push('/cart')
                        }}
                        data-seed="logId1"
                    ></TabBar.Item>


                    <TabBar.Item
                        icon={
                            <i className={`${this.state.str}`} onClick={()=>{
                                return (this.props.userinfo
                                ? <div>
                                    {this.getlist()}
                                    </div>
                                :
                                        this.props.history.push('/login')
                                )
                                }}></i>
                        }
                        title="收藏"
                        key="cart"
                    
        
                        data-seed="logId1"
                    ></TabBar.Item>

                    <TabBar.Item title=""
                        title=""
                        key="home"
                        icon={
                            <div style={{backgroundColor:'orange',
                            borderTopRightRadius:'0px',borderTopLeftRadius:'20px',
                            borderBottomLeftRadius:'20px',borderBottomRightRadius:'0px',
                            width:'100px',height:'35px'}}>
                               <span style={{ display:'inline-block',color:'white',marginTop:'9px'}}>加入购物车</span>
                            </div>
                          }
                    >

                    </TabBar.Item>

                    <TabBar.Item
                        title=""
                        key="home"
                        icon={
                    
                            <div style={{backgroundColor:'#FF6600',
                            borderTopRightRadius:'20px',borderTopLeftRadius:'0px',
                            borderBottomLeftRadius:'0px',borderBottomRightRadius:'20px',
                            width:'100px',height:'35px'}}>
                               <span style={{ display:'inline-block',color:'white',marginTop:'9px'}}>立即购买</span>
                            </div>
                          
                           
                        }
                    >
                    </TabBar.Item>

                </TabBar>
            </div>
        );
    }
    }

    const dldl = (state)=>{
        return {
           sc:state.sc,
            userinfo:state.login,
            index:state.change,
            datalist:state.todo
        }
    }

export default connect(dldl)(withRouter(Buyit));