import React from 'react';
import { Flex, WhiteSpace } from 'antd-mobile';
import  {withRouter} from 'react-router-dom';
import Buyit from './Buyit';
import {connect} from 'react-redux';


const Goods= (props) => {
    console.log(props);
    let index=props.change;
    const imgobj=props.mydata[index];
    const str=imgobj.img+'';
    console.log(str);
    return (

            <div>
                <span onClick={() => props.history.goBack("/home")}>{`<-`}</span>
                <div className="flex-container">
                    <div className="sub-title"></div>
                    <Flex>
                        <Flex.Item><img src={str}>
                        </img>
                        </Flex.Item>
                    </Flex>
                </div>
               <Buyit />
            </div>
        )
    }

    const con = (state)=>{
        return {
            mydata:state.todo,
            change:state.change
        }
    }
export default connect(con)(withRouter(Goods));