import React from 'react';
import {HashRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Header from './pages/Header';
import routes from './router.config';
import {Button,TabBar} from 'antd-mobile'
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Sc from './taobao/Sc';
import Login from './taobao/Login';
import Searchturn from './taobao/Searchturn';
import Goods from './taobao/Goods'
import store from './taobao/Store';
import {Provider} from 'react-redux';
const App = ()=>{
	return (
<Provider store={store
}> 
	<Router>
		<Switch>
			<Route exact path='/' component={Home} />
			<Route path='/cart' component={Cart} />
			<Route path='/mytab' component={Mytab} />
			<Route path='/searchturn' component={Searchturn} />
			<Route path='/goods' component={Goods} />
			<Route path='/login' component={Login} />
			<Route path='/sc' component={Sc} />
		</Switch>
		{/* <Mytab /> */}
	</Router>
	</Provider>
	)
}
	
export default App;

