import React from 'react';
import { withRouter } from 'react-router-dom';

import { Flex, WhiteSpace } from 'antd-mobile';
import { render } from 'react-dom';
import { Component } from 'react';
import {connect} from 'react-redux';      
import store from './Store';

class FlexExample extends React.Component {
    constructor(props) {
        super();

    }
    state = {
        imgObj: [],

    }
    componentDidMount = () => {
        fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist')
            .then((res) => res.json())
            .then((myJson) => {
                console.log(myJson);
                let values = myJson.data;
                //
                this.props.dispatch({
                    type:'ADD_ITEM',
                    value:values
                })
                this.props.dispatch({
                    type:'AD_TODO',
                    value:values
                })

                
                this.setState({
                    imgObj: values
                })
            });
         
    }
     
    getimage(idx) {
         let arr = this.state.imgObj;
        for (var i = idx; i < arr.length; i++) {
            return (
                 <Flex>
                    <Flex.Item><img style={{ width: '200px', height: '200px', }}
                        onClick={() => {
                            
                            this.props.dispatch({
                                type:'AD_ITEM',
                                index:i
                            })
                            
                            this.props.history.push("/goods")}}
                        src={arr[i].img} ></img><p>{arr[i].price}</p><p>{arr[i].title}</p>
                    </Flex.Item>
                    <Flex.Item><img style={{ width: '200px', height: '200px' }}
                        onClick={() => {
                            this.props.dispatch({
                                type:'AD_ITEM',
                                 index:i+1
                            })
                            this.props.history.push("/goods")}}
                        src={arr[i + 1].img}></img><p>{arr[i + 1].price}</p><p>{arr[i + 1].title}</p></Flex.Item>
                 </Flex>
                

            )
        }
}

    render() {
      
        console.log("hhh:", this.props.userinfo);
        return (
            <div>
                <div className="flex-container">
                    <div className="sub-title">
                        {this.getimage(0)}
                        {this.getimage(2)}
                        {this.getimage(4)}
                        {this.getimage(6)}
                        {this.getimage(8)}
                       
                    </div>
                </div>
            </div>







        )
    }

}

const mapStateToProps = (state)=>{
    return {
        mydata:state.todo,
        change:state.change,
        userinfo:state.login
    }
}

export default connect(mapStateToProps)(withRouter(FlexExample));